.btn-main {
  @include flexCenter;
  @include fontProperties("medium", 16px);
  color: white;
  text-align: center;
  width: 186px;
  height: 50px;
  cursor: pointer;

  border-radius: 25px;
  border: none;
  background: linear-gradient(
    193.33deg,
    $primary-color -11.44%,
    $btn-main-lightcolor 123.93%
  );

  &:hover {
    opacity: 0.8; 
  }
}




