.restaurant-menu {
  position: relative;
  &__img {
    img {
      width: 100%;
      height: 275px;
      object-fit: cover;
      object-position: center;
      position: absolute;
      top: -220px;
      z-index: -7;
    }
  }

  @include notMobile {
    &__img {
      width: 100%;
     
      img {
        width: 100%;
        height: 375px;
        top: -343px;
      }
    }
  }
}

